import React from "react"
import { useShoppingCart } from "use-shopping-cart"
import 'bootstrap/dist/css/bootstrap.min.css';
import './index.css'
import Cart from "../components/cart";
import { navigate } from "gatsby";
export default function CartPage() {
    console.log("Cart Page")
    const { totalPrice, cartDetails, incrementItem, decrementItem, redirectToCheckout } = useShoppingCart();

    if (!Object.keys(cartDetails)) {
        return (
            <div> Cart is Empty</div>
        )
    }
    return (

        <>
            <div className="container">
                <table id="cart" className="table table-hover table-condensed">
                    <thead>
                        <tr>
                            <th >Product</th>
                            <th >Price</th>
                            <th >Quantity</th>
                            <th className="text-center">Subtotal</th>
                            <th></th>
                        </tr>
                    </thead>
                    <tbody>
                        {Object.keys(cartDetails).map((item) => (
                            <tr>
                                <td data-th="Product">
                                    <div className="row">
                                        <div className="col-sm-2 hidden-xs"><img src={cartDetails[item].image} alt="..." className="img-responsive" /></div>
                                        <div className="col-sm-10">
                                            <h4 className="nomargin">{cartDetails[item].name}</h4>
                                            <p>{cartDetails[item].description}</p>
                                        </div>
                                    </div>
                                </td>
                                <td data-th="Price">{cartDetails[item].price/100}</td>
                                <td data-th="Quantity">
                                    <input type="button" className="form-control text-center" readOnly defaultValue={cartDetails[item].quantity}  />
                                </td>
                                <td data-th="Subtotal" className="text-center">{(cartDetails[item].price/100) * cartDetails[item].quantity}</td>
                                <td className="actions" data-th="">
                                    <button className="btn btn-info btn-sm" onClick={() =>incrementItem(cartDetails[item].sku)}><i className="fa fa-refresh"></i>+</button>
                                    <button className="btn btn-danger btn-sm" onClick={()=> decrementItem(cartDetails[item].sku)}><i className="fa fa-trash-o"></i>-</button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                    <tfoot>

                        <tr>
                            <td><button onClick={() =>navigate("/")} className="btn btn-warning"> Continue Shopping</button></td>
                            <td colSpan="2" className="hidden-xs"></td>
                            <td className="hidden-xs text-center"><strong>Total ${totalPrice/100}</strong></td>
                            <td><button onClick={()=>redirectToCheckout()} className="btn btn-success btn-block">Checkout </button></td>
                        </tr>
                    </tfoot>
                </table>
            </div>

            <div>
                <h1>Cart </h1>
                <Cart />
                <button onClick={() => redirectToCheckout()}>Go To Checkout</button>
                <hr />
                {Object.keys(cartDetails).map((item) => (
                    <div>
                        <p> Name : {cartDetails[item].name}</p>
                        <p> Name : {cartDetails[item].price}</p>
                        <p> Name : {cartDetails[item].quantity}</p>
                        <button onClick={() => {
                            incrementItem(cartDetails[item].sku)
                        }}>+</button>
                        <button onClick={() => {
                            decrementItem(cartDetails[item].sku)
                        }}>-</button>
                    </div>
                ))}
            </div>
        </>
    )
}
